.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
}

.App-title {
  text-align: center;
  color: white;
  font-size: 5em
}

.search-title {
  text-align: center;
  color: white;
  font-size: 3em
}

.small-title {
  text-align: center;
  color: white;
  font-size: 2em
}

.nav-bg{
  background-color: #717D7E;
}
.nav-title{
  color: rgb(9, 80, 70);
  font-size: 1.5em;
  font-weight: 900;
}

.mcu{
  color: #ED1D24;
  font-weight: 500;
}

.starwars{
  color: #FFE81F;
  font-weight: 500;
}

.matrix{
  color:  #00FF41;
  font-weight: 500;
}

.Red{
  color: #ED1D24;
}

.cardw{
  width:300px
}
.cardw1{
  width:100%
}

.cardb{
  background-color: black;
}

.padding{
  padding: 3em;
  padding-left: 5em;
  align-self: flex-start 
  
}
.flex{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.welcome{
  min-height: 100vh;
  background: linear-gradient( rgba(0,0,0,0.5), rgba(0, 0, 0, 0.5) ), url(".\images\wp1945898-movie-posters-wallpapers.jpg");
  align-content: center;
  padding-top: 50px;
}

.center{
  align-self: center;
  margin-left: 38em;
}
.a:hover{
  color: inherit;
}
